<template>
    <div class="register">
        <div class="row">
            <div class="col s12 m10 offset-m1 l8 offset-l2 xl6 offset-xl3">
                <div class="card-panel hoverable">
                    <h3 class="justificado">Registra una canchita</h3>
                    <form>
                        <div>
                            <div class="row">
                                <div class="input-field col s12">
                                    <input id="first_name" type="text" class="validate">
                                    <label for="first_name">Nombre</label>
                                </div>
                                <div class="input-field col s12">
                                    <input id="last_name" type="text" class="validate">
                                    <label for="last_name">Dirección</label>
                                </div>
                                <div class="input-field col s12">
                                    <input id="dni" type="text" class="validate">
                                    <label for="dni">Telefono</label>
                                </div>
                                <div class="input-field col s12">
                                    <div class="file-field input-field">
                                        <div class="btn waves-effect green">
                                            <span>Imagen</span>
                                            <input type="file" id="file-upload" accept="image/*">
                                        </div>
                                        <div class="file-path-wrapper">
                                            <input class="file-path validate" type="text">
                                        </div>
                                        <div id="file-preview-zone" class="img-contenedor">

                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="input-field col s12">
                            <input type="submit" value="Registrar" class="btn waves-effect green">
                            </div>
                        </div>
                    </form>
                </div>
            </div>
            
        </div>
    </div>
</template>

<script>
export default {
    name : "register",
    data : function(){
        return {
        }
    },
    methods : {
    },
    mounted: function () {
		this.$nextTick(function () {
            let fileUpload = document.getElementById('file-upload');
            fileUpload.onchange = function (e) {
                let input = e.srcElement;
                if (input.files && input.files[0]) {
                    let reader = new FileReader();
                    reader.onload = function (e) {
                        let filePreview = document.createElement('img');
                        filePreview.id = 'file-preview';
                        filePreview.src = e.target.result;
                        console.log(e.target.result);
                        let previewZone = document.getElementById('file-preview-zone');
                        previewZone.innerHTML = "";
                        previewZone.appendChild(filePreview);
                    }
                    reader.readAsDataURL(input.files[0]);
                } 
            }
		});
	}
}
</script>

<style scoped>
 div.img-contenedor img{
     width: 100%;
 }
</style>
